import { GamesProvider } from './../../providers/games/games';
import { AuthenicationProvider } from './../../providers/authenication/authenication';
import { Global } from './../../providers/global';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-games',
  templateUrl: 'games.html',
})
export class GamesPage {
  gamelist: any; 
  constructor(public navCtrl: NavController,
    private globalProvider: Global,
    private authProvider: AuthenicationProvider,
    private gamesProvider: GamesProvider,
   private actionSheetCtrl:ActionSheetController,
    public navParams: NavParams) {
  }

  ionViewWillEnter() {
    const currentdate = this.globalProvider.getDate();
    this.GetGames(currentdate);
  }

  GetGames(currentdate) {
    this.gamesProvider.GetGames(currentdate)
      .subscribe(resp => {
        if (resp.statusCode === 200) {
          console.log(resp);
          this.gamelist = resp.data;
        } else {
          this.authProvider.showToast(resp.description);
        }
      }, error => {
        this.authProvider.showToast(error.error.description);
      });
  }

  onGameOption(game) {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Game Options',
      buttons: [
        {
          text: 'View / Edit',
          handler: () => { this.navCtrl.push('GameEditPage', {game}) }
        }, {
          text: 'Delete',
          handler: () => { this.onDeleteGame(game) }
        }, {
          text: 'Cancel',
          role: 'cancel',
          handler: () => { }
        }
      ]
    });
    actionSheet.present();
  }


  onDeleteGame(game){
    console.log(game)
  }
}



